

import { Options, Vue } from 'vue-class-component';
import { Tip } from '@/store/data/data-types';
import { Prop } from 'vue-property-decorator';
import { QuillEditor } from '@vueup/vue-quill';
import TippArea from '../menus/tipps/tipp-area.vue';
import '@vueup/vue-quill/dist/vue-quill.snow.css';

@Options({
  components: {
    TippArea,
    QuillEditor,
  },
})
export default class Question extends Vue {
  @Prop({ type: String })
  private text = ''

  @Prop({ type: String })
  private heading = ''

  @Prop({})
  private tipps: Tip[] = []

  @Prop({})
  private canvasId = 'test'

  getCanvasId(): string {
    return this.canvasId;
  }

  private penIcon = 'pen';

  private showCanvas = false;

  private textEditorOptions = {
    debug: 'info',
    placeholder: 'Tippe eine Antwort',
    readOnly: false,
    theme: 'snow',
    bounds: '.center-col',
  }

  mounted(): void {
    this.init();
  }

  private started = false;

  private context = {} as CanvasRenderingContext2D;

  private canvas = {} as HTMLCanvasElement

  // Initialization sequence.
  init():void {
    // Find the canvas element.
    this.canvas = document.getElementById(this.canvasId) as HTMLCanvasElement;
    if (!this.canvas) {
      console.log('Error: I cannot find the canvas element!');
      return;
    }

    if (!this.canvas.getContext) {
      console.log('Error: no canvas.getContext!');
      return;
    }

    // Get the 2D canvas context.
    if (this.canvas.getContext('2d')) {
      this.context = this.canvas.getContext('2d') as CanvasRenderingContext2D;
    } else {
      console.log('Error: failed to getContext!');
      return;
    }

    // Attach the mousedown, mousemove and mouseup event listeners.
    this.canvas.addEventListener('mousedown', this.evMouseDownCanvas, false);
    this.canvas.addEventListener('mousemove', this.evMouseMoveCanvas, false);
    this.canvas.addEventListener('mouseup', this.evMouseUpCanvas, false);
    this.canvas.addEventListener('touchstart', this.evTouchDownCanvas, false);
    this.canvas.addEventListener('touchmove', this.evTouchMoveCanvas, false);
    this.canvas.addEventListener('touchend', this.evTouchEndCanvas, false);
  }

  evMouseDownCanvas(ev:MouseEvent):void {
    const pos = this.getMousePos(this.canvas, ev);
    this.context.beginPath();
    this.context.moveTo(pos.x, pos.y);
    this.started = true;
  }

  evMouseMoveCanvas(ev:MouseEvent):void {
    const pos = this.getMousePos(this.canvas, ev);
    if (this.started) {
      this.context.lineTo(pos.x, pos.y);
      this.context.stroke();
    }
  }

  evMouseUpCanvas(ev:MouseEvent):void {
    if (this.started) {
      this.evMouseMoveCanvas(ev);
      this.started = false;
    }
  }

  evTouchDownCanvas(ev:TouchEvent):void {
    const pos = this.getTouchPos(this.canvas, ev);
    this.context.beginPath();
    this.context.moveTo(pos.x, pos.y);
    this.started = true;
  }

  evTouchMoveCanvas(ev:TouchEvent):void {
    const pos = this.getTouchPos(this.canvas, ev);
    if (this.started) {
      this.context.lineTo(pos.x, pos.y);
      this.context.stroke();
    }
  }

  evTouchEndCanvas(ev:TouchEvent):void {
    if (this.started) {
      this.evTouchMoveCanvas(ev);
      this.started = false;
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  getMousePos(canvas: HTMLCanvasElement, evt: MouseEvent): any {
    const rect = canvas.getBoundingClientRect();
    return {
      x: evt.clientX - rect.left,
      y: evt.clientY - rect.top,
    };
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  getTouchPos(canvas: HTMLCanvasElement, evt: TouchEvent): any {
    const rect = canvas.getBoundingClientRect();
    return {
      x: evt.touches[0].clientX - rect.left,
      y: evt.touches[0].clientY - rect.top,
    };
  }
}
