
import { Ressource, RessourceType } from '@/store/data/data-types';
import { Options, mixins } from 'vue-class-component';
import { Prop, Watch } from 'vue-property-decorator';
import SourceSelector from './source-selector.vue';
import ImageRessource from '../content/ressources/ImageRessource.vue';
import TextRessource from '../content/ressources/TextRessource.vue';
import AudioRessource from '../content/ressources/AudioRessource.vue';
import VideoRessource from '../content/ressources/VideoRessource.vue';

@Options({
  components: {
    ImageRessource,
    TextRessource,
    VideoRessource,
    AudioRessource,
  },
})
export default class Source extends mixins(SourceSelector) {
  @Prop({ type: String })
  private ressourceId = '';

  @Prop({ })
  private type: RessourceType = RessourceType.IMAGE_SOURCE;

  public currentRessource: Ressource = {} as Ressource;

  @Watch('ressourceId')
  onSourceIdChange(value: string): void {
    this.currentRessource = this.getCurrentRessource(value, this.type);
  }

  @Watch('type')
  onTypeChange(value: string): void {
    // TODO: implement get ID out of URL instead of hardcoded 0
    this.currentRessource = this.getCurrentRessource('0', value);
  }

  created(): void {
    this.currentRessource = this.getCurrentRessource(this.ressourceId, this.type);
  }

  getComponentFromType(): string {
    switch (this.type) {
      case RessourceType.IMAGE_SOURCE:
        return 'ImageRessource';
      case RessourceType.TEXT_SOURCE:
        return 'TextRessource';
      case RessourceType.VIDEO_SOURCE:
        return 'VideoRessource';
      case RessourceType.AUDIO_SOURCE:
        return 'AudioRessource';
      default:
        return '';
    }
  }
}
